@charset "UTF-8";
:root {
  --primary: #1d3faa;
  --textColor: #FFF;
}

.sidebar {
  margin-top: 50px;
  background: #ffffff !important;
}
.sidebar nav {
  overflow-y: auto;
}
.sidebar nav ul li {
  padding: 0 !important;
}
.sidebar nav ul li a i {
  color: var(--primary) !important;
}
.sidebar nav ul li a:hover, .sidebar nav ul li a.active {
  background: var(--primary) !important;
  border-left: none !important;
}
.sidebar nav ul li a:hover i, .sidebar nav ul li a.active i {
  color: #ffffff !important;
}
.sidebar nav ul li a:hover span, .sidebar nav ul li a.active span {
  color: #ffffff !important;
}
.sidebar .aside-bottom ul li a {
  color: #666 !important;
  border-top: solid 1px #9c9c9c !important;
}
.sidebar .aside-bottom ul li a i {
  color: var(--primary) !important;
}
.sidebar .aside-bottom ul li a:hover, .sidebar .aside-bottom ul li a.active {
  background: var(--primary) !important;
}
.sidebar .aside-bottom ul li a:hover i, .sidebar .aside-bottom ul li a.active i {
  color: #ffffff !important;
}
.sidebar .aside-bottom ul li a:hover span, .sidebar .aside-bottom ul li a.active span {
  color: #ffffff !important;
}
.sidebar.with-text {
  width: 140px !important;
}
.sidebar.with-text nav ul li {
  padding: 0 0 1px 0;
}
.sidebar.with-text nav ul li a {
  position: relative;
  text-align: center;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 7px 0 !important;
  color: #666 !important;
  transition: all 0.3s;
}
.sidebar.with-text nav ul li a i {
  color: var(--primary) !important;
  display: block;
  font-size: 22px;
  margin-bottom: 6px;
  transition: all 0.3s;
}
.sidebar.with-text nav ul li a span:not(.ivu-badge) {
  color: #666 !important;
  font-size: 11px;
  font-weight: 500;
  line-height: 12px;
  padding-left: 5px;
  padding-right: 5px;
  display: block;
  transition: all 0.3s;
}
.sidebar.with-text nav ul li a .ivu-badge {
  position: absolute;
  top: 3px;
  right: 10px;
}
.sidebar.with-text nav ul li a:hover {
  border-left: 3px solid var(--primary) !important;
  background: var(--primary) !important;
}
.sidebar.with-text nav ul li a:hover i {
  color: #ffffff !important;
}
.sidebar.with-text nav ul li a:hover span {
  color: #ffffff !important;
}
.sidebar.with-text nav ul li a.active {
  border-left: 3px solid var(--primary) !important;
  background: var(--primary) !important;
}
.sidebar.with-text nav ul li a.active i {
  color: #ffffff !important;
}
.sidebar.with-text nav ul li a.active span {
  color: #ffffff !important;
}
.sidebar .logo {
  background: #ffffff;
  height: 50px !important;
}
.sidebar .logo img {
  width: 40px !important;
}
.sidebar .logo h1 {
  color: #ffffff;
  font-size: 16px;
}

.sub-header-left {
  width: auto;
}
.sub-header-left h3.title {
  border-top: solid 3px white !important;
}
.sub-header-right ul > li.avatar-item .ivu-poptip-content .ivu-poptip-arrow {
  border-bottom-color: var(--primary) !important;
}
.sub-header-right ul > li.avatar-item .ivu-poptip-content .ivu-poptip-arrow:after {
  border-bottom-color: var(--primary) !important;
}
.sub-header-right ul > li.avatar-item .ivu-poptip-content .ivu-poptip-body .header-profile-info {
  background: var(--primary) !important;
}

.sub-sidebar nav ul li a:hover {
  color: #666 !important;
}
.sub-sidebar nav ul li a.active {
  font-weight: 400 !important;
  color: #666 !important;
}
.sub-sidebar nav ul li a.active:hover {
  color: #666 !important;
  cursor: default;
}
.sub-sidebar nav ul li a.active i {
  opacity: 1;
}

.sub-sidebar-top {
  padding: 10px !important;
}
.sub-sidebar-top h2 {
  line-height: 18px !important;
}

.sub-nav-list .card-body {
  max-height: calc(100% - 90px);
  overflow-y: auto;
  max-width: 280px;
}

.moqup-title {
  margin-bottom: 0.5rem;
  margin-top: 0px;
  margin-left: 0px;
  margin-right: 0px;
  padding: 14px 30px !important;
  color: #e84a06 !important;
  background: #ece8fd !important;
}

.crud-page-body .v-nav .card-body li a {
  transition: all 0s ease !important;
}

.crud-page-body .v-nav .card-body li a.active {
  color: var(--primary) !important;
}

.crud-page-body .v-nav .card-body li a:hover {
  color: var(--primary) !important;
}

.crud-page-body .v-nav .card-header {
  background: #ffffff !important;
}

.iframe-page {
  height: calc(100vh - 90px) !important;
  padding: 20px;
  margin-top: 50px;
  background: #F1F5F8;
  flex: 1;
  overflow-y: auto;
  outline: none !important;
}
.iframe-page:focus {
  outline: none !important;
}
.iframe-page iframe {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  border: none;
  outline: none !important;
  outline-style: none;
  box-shadow: none;
  border-color: transparent;
}
.iframe-page iframe .moqup-title {
  display: none;
}
.iframe-page iframe:focus {
  outline: none !important;
}

.page-report {
  padding-top: 50px;
}

.analytic {
  background-color: #F1F5F8 !important;
}

