.bums-home {
  height: 100vh;
  background-color: #ffffff;
  overflow-y: auto;
}
.bums-home .logo {
  border-right: none !important;
  box-shadow: none !important;
  padding-right: 0 !important;
}
.bums-home .logo a {
  display: flex;
  flex-direction: row;
  padding-top: 0px;
  justify-content: center;
}
.bums-home .logo a img {
  width: auto !important;
  max-width: 100%;
  height: auto;
  max-height: 125px;
  margin-top: 0px;
}
.bums-home .brand-txt {
  display: flex;
  flex-direction: column;
  padding-top: 8px;
  margin-left: 25px;
}
.bums-home .brand-txt h1 {
  color: var(--primary);
  font-size: 21px;
  margin: 0;
  line-height: 21px;
}
.bums-home .brand-txt h2 {
  font-size: 16px;
  color: var(--primary);
  font-weight: 400;
  margin: 0;
  line-height: 20px;
}
.bums-home .material .crud-page-header {
  box-shadow: none !important;
}
.bums-home .footer {
  position: fixed;
  left: 0px;
  bottom: 0px;
  height: 50px;
  width: 100%;
  background: #fff;
  text-align: right;
  line-height: 50px;
  padding-right: 20px;
  color: var(--primary);
}

.bums-container {
  width: 1170px;
  display: block;
  margin: 0 auto;
  min-width: 800px;
}
.bums-container .b-row {
  display: flex;
}
.bums-container .b-item {
  width: 25%;
  height: 200px;
  transition: all 0.3s ease;
  padding: 50px 0;
  text-align: center;
  color: #333;
}
.bums-container .b-item img {
  width: 60px;
  transition: all 0.3s ease;
}
.bums-container .b-item h3 {
  font-weight: 400;
  margin-top: 20px;
  text-transform: uppercase;
  transition: all 0.3s ease;
}
.bums-container .b-item .b-info {
  color: #9c9c9c;
  max-width: 80%;
  text-align: center;
  margin: 0 auto;
  opacity: 0;
  transition: all 0.3s ease;
}
.bums-container .b-item:hover {
  box-shadow: 0 20px 52px -20px rgba(0, 0, 0, 0.5);
  background-color: var(--primary);
}
.bums-container .b-item:hover img {
  transform: scale(0.8) translateY(-45%);
  filter: brightness(0) invert(1);
}
.bums-container .b-item:hover h3 {
  transform: translateY(-100%);
  color: #fff;
}
.bums-container .b-item:hover .b-info {
  transform: translateY(-50%);
  opacity: 1;
  color: #fff;
}

.bums-brand {
  text-align: left;
  display: block;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 60px;
  margin-top: -20px;
}
.bums-brand img {
  width: 100px;
}
.bums-brand .manai-khoroo {
  margin-left: 20px;
  width: 280px !important;
  height: auto;
}
.bums-brand .sponsor {
  float: right;
  width: 480px !important;
  height: auto;
}
.bums-brand h2 {
  font-weight: 400;
  line-height: 100px;
  color: var(--primary);
  margin-left: 40px;
  font-size: 21px;
}

.border-default-left {
  border-left: 1px solid var(--primary);
}

.border-default-top {
  border-top: 1px solid var(--primary);
}

header {
  height: 50px !important;
  background: var(--primary) !important;
  z-index: 10 !important;
  padding: 0;
  top: 1.25rem !important;
  left: 2.3em !important;
  width: calc(100% - 3.7em) !important;
  position: absolute !important;
}
header .wbg {
  background-color: #F1F5F8;
  position: absolute;
  left: 270px;
  width: calc(100% - 300px);
  height: 50px;
  z-index: 0;
}
header .wbg-border {
  background-color: #e2e8f0;
  height: 1px;
  z-index: 2;
  left: 260px;
  width: calc(100% - 280px);
  bottom: 0;
  position: absolute;
}
header .logo {
  background-color: var(--primary);
  border-right: none !important;
  box-shadow: none !important;
  width: 228px;
  min-width: 228px;
  max-width: 228px;
  margin-left: 0px;
  margin-right: 20px;
  padding-left: 10px;
  padding-right: 10px !important;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
header .logo a {
  display: flex;
  flex-direction: row;
  padding-top: 0px;
}
header .logo a img {
  width: auto !important;
  max-width: 100%;
  height: auto;
  max-height: 50px;
  margin-top: 0px;
  margin-left: 0 !important;
}
header .side-toggle {
  color: var(--primary);
  margin: 13px 10px 0 0px;
}
header .side-toggle i {
  font-size: 25px;
}
header .side-toggle:hover {
  color: #001E35;
}
header .header-left {
  display: flex;
  padding: 0 10px;
  background-color: #F1F5F8;
  border-top-left-radius: 25px;
  height: 50px;
  line-height: 50px;
  z-index: 1;
}
header .header-left h3 {
  font-weight: 400;
  font-size: 14px;
  color: var(--primary);
  width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
}
header .header-left button {
  height: 28px;
  margin-top: 10px;
}
header .hidden-page-title h3, header .hidden-page-title .divider {
  display: none;
}
header .header-right {
  margin-left: auto;
  display: flex;
  padding: 0 10px;
  height: 50px;
  line-height: 50px;
  background-color: #F1F5F8;
  border-top-right-radius: 25px;
  z-index: 1;
}
header .user-control {
  border-left: none !important;
}
header .vue-portal-target {
  display: flex;
}
header .divider {
  height: 20px;
  width: 1px;
  background: #dedede;
  margin: 15px 10px;
}
header .brand-txt {
  display: flex;
  flex-direction: column;
  padding-top: 8px;
}
header .brand-txt h1 {
  color: var(--primary);
  font-size: 18px;
  margin: 0;
  line-height: 18px;
}
header .brand-txt h2 {
  font-size: 14px;
  color: var(--primary);
  font-weight: 400;
  margin: 0;
  line-height: 14px;
}

.aside-page header {
  border-bottom: none;
}

.top-nav {
  position: absolute;
  top: 80px;
  left: 56px;
  width: calc(100% - 170px);
  text-align: center;
  height: 50px;
  background: var(--primary);
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  display: flex;
  flex-direction: row;
}
.top-nav ul {
  display: flex;
  flex-direction: row;
}
.top-nav ul li {
  list-style: none;
  line-height: 50px;
  padding: 0;
}
.top-nav ul li a {
  height: 50px;
  display: block;
  color: #F1F5F8;
  padding: 0 10px !important;
}
.top-nav ul li a i {
  color: #F1F5F8 !important;
}
.top-nav ul li a:hover {
  background: #244fd6 !important;
  position: relative;
  border-radius: 10px;
}
.top-nav ul li a.active {
  background: #F1F5F8 !important;
  position: relative;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  border-left: none !important;
}
.top-nav ul li a.active:hover {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.top-nav ul li a.active i {
  color: var(--primary) !important;
}
.top-nav ul li a.active span {
  color: var(--primary) !important;
}
.top-nav ul li a.active:before {
  content: "";
  width: 20px;
  height: 20px;
  margin-left: -20px;
